@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500;900&display=swap);
.app {
  text-align: center;

  display: flex;
  flex-direction: column;
  align-items: center;
}

h1{
  margin: 30px 0;

  color: #FCBF49;
  font-size: 4.5rem;

  text-decoration: underline;
  text-transform: uppercase;
}

.smallclocks{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 95vw;
}

footer{
  margin-top: 50px;
  padding-bottom: 50px;
  color:#FCBF49;
}

footer a{
  margin-left: 5px;
}

footer a:link, footer a:visited{
  margin-left: 5px;
  color: #EAE2B7;
}

@media (max-width: 600px){
  h1{
      font-size: 1.6rem;
  }
}
@keyframes preloaderWheel{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}

.clock{
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin-bottom: 40px;
}

.clock__time{
    font-size: 9vw;
}

.clock__time__number{
    font-size: 15vw;
    font-weight: 900;
    letter-spacing: -0.05em;
    font-family: 'Roboto Mono', monospace;
    margin: 0 10px;
    text-align: center;
}

.clock__description{
    color: #FCBF49;
    font-weight: 700;
    font-size: 2rem;
    margin-top: -20px;
}

.clock__preloader{
    width: 30px;
    height: 30px;

    margin-top: 80px;

    border-radius: 50%;
    border-top: 6px rgb(77, 77, 77) solid;
    border-right: 6px rgb(77, 77, 77) solid;
    border-left: 6px transparent solid;
    border-bottom: 6px transparent solid;

    animation: preloaderWheel .3s ease-in-out 0s infinite forwards;
}

@media (max-width: 600px){
    .clock__description{
        font-size: 1.3rem;
        margin-top: 0px;
    }
}
@keyframes preloaderWheel{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}

.small_bolder{
    font-weight: 700;
    color: #FCBF49;
}

.smallclock{
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: 220px;
    height: 70px;
    margin: 15px;
    background-color: #D62828;
}

.smallclock__time{
    font-size: 1rem;
    color: #FCBF49;
}

.smallclock__time__number{
    font-size: 1.6rem;
    font-weight: 900;
    letter-spacing: -0.05em;
    font-family: 'Roboto Mono', monospace;
    text-align: center;
}

.smallclock__description{
    color: #EAE2B7;
    font-size: 1rem;
}

.smallclock__preloader{
    width: 30px;
    height: 30px;

    border-radius: 50%;
    border-top: 6px rgb(77, 77, 77) solid;
    border-right: 6px rgb(77, 77, 77) solid;
    border-left: 6px transparent solid;
    border-bottom: 6px transparent solid;

    animation: preloaderWheel .3s ease-in-out 0s infinite forwards;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  min-height: 100vh;
  font-size: 18px;

  background-color: #003049;
  color: #F77F00;
}
